:root {
  --blue: blue;
  --red: red;
  --white: white;
  --black: black;
  --grey: #aaa;
  --primary-color: var(--blue);
  --secondary-color: var(--white);
  --shadow: rgba(0, 0, 0, 0.7);
}

.editor-styles-wrapper .wp-block {
  max-width: unset !important;
  margin-left: 2rem;
  margin-right: 2rem;
}

.rounded {
  border-radius: 0.5rem;
}

.btn {
  display: inline-block;
  border-radius: 0.25rem;
  padding: 1.5rem !important;
  text-decoration: none;
  line-height: 0;
  appearance: none;
}

.btn--primary {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border: solid 1px var(--primary-color);
}

.btn--primary:hover {
  opacity: 0.7;
  color: var(--secondary-color);
  cursor: pointer;
}

.form__container--selection {
  padding: 4rem;
  outline: dashed 2px var(--grey);
}

.form-control {
  padding-bottom: 1rem;
}
.form-control .form-label {
  display: block;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
}
.form-control .form-input {
  padding: 0.5rem 1rem !important;
  width: 100%;
}

.hero .hero__container {
  position: relative;
}
.hero .hero__title {
  font-size: 4rem;
  margin: 0;
}
.hero .hero__description {
  font-size: 1.75rem;
  margin: 0;
}
.hero .hero__button {
  margin-top: 1rem;
}

.hero.hero--full .hero__container {
  padding: 10rem 4rem;
  z-index: 1;
}
.hero.hero--full .hero__title {
  color: var(--white);
  margin: 0;
}
.hero.hero--full .hero__description {
  margin: 0;
  color: var(--white);
}
.hero.hero--full .hero__figure {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.hero.hero--full .hero__figure::before {
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.4);
  content: "";
  z-index: 2;
}
.hero.hero--full .hero__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero.hero--split .hero__container {
  display: flex;
}
.hero.hero--split .hero__content {
  width: 50%;
}
.hero.hero--split .hero__figure {
  max-width: 50%;
  margin-left: 2rem;
}

.hero.hero--split.hero--left .hero__container {
  flex-direction: row-reverse;
}
.hero.hero--split.hero--left .hero__figure {
  margin-left: 0rem;
  margin-right: 2rem;
}

.alert {
  background-color: var(--red);
  color: var(--white);
  border-radius: 0.5rem;
  padding: 1.5rem;
  text-align: center;
}

.instagram-feed {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem;
}

.instagram-feed .card {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  border-radius: 0.5rem;
}

.instagram-feed .card__body {
  padding: 1rem;
  font-size: 1rem;
}

.instagram-feed .card__title {
  font-size: 1rem;
  padding-top: 0.5rem;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.instagram-feed .card__figure {
  position: relative;
  width: 100%;
  height: 320px;
  margin: 0;
}
.instagram-feed .card__figure .card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin: 0;
}